.header {
  padding: vw(30) vw(50);
  margin-bottom: vw(200);
  @extend %flex-center-between;
  &-logo {
    @extend %block;
    @extend %zero-font-size;
    width: vw(300);
  }
  &-menu {
    ul {
      list-style: none;
      @extend %zero-font-size;
    }
    li {
      display: inline-block;
      vertical-align: top;
    }
    a {
      text-decoration: none;
      margin: 0 vw(20);
      padding: vw(20) 0;
      font-weight: 600;
      font-size: vw(20);
      line-height: vw(20);
      @extend %white;
      @extend %block;
    }
  }
  &-lang {
    @extend %zero-font-size;
    li {
      font-weight: 500;
      font-size: vw(20);
      line-height: vw(20);
      @extend %white;
      @extend %inline-block;
    }
    a {
      font-weight: inherit;
      font-size: inherit;
      line-height: inherit;
      color: inherit;
      text-decoration: none;
      @extend %transition;
      &:hover {
        color: $mainColor;
      }
    }
  }
}
