.main-question {
  padding: vw(120) vw(50) vw(514);
  background: #fff;
  position: relative;

  &-tentacle {
    height: vw(1498);
    position: absolute;
    z-index: 1;
    left: 0;
    right: 0;
    top: 0;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 50% vw(-200);
    }
  }
  &-title {
    text-transform: uppercase;
    margin-bottom: vw(60);
    position: relative;
    z-index: 2;
    
    @extend %white;
  }
  &-subtitle {
    font-size: vw(30);
    line-height: vw(30);
    margin-bottom: vw(50);
    position: relative;
    z-index: 2;
    
    @extend %white;
  }
  &-form {
    position: relative;
    z-index: 2;
    
  }
  &-inputs {
    font-size: 0;

    input {
      box-sizing: border-box;
      background: transparent;
      border: none;
      padding: vw(20) 0;
      outline: none;
      &:not(:last-child) {
        margin-right: vw(136);
      }
    }
    input[type="text"],
    input[type="tel"],
    input[type="email"] {
      width: calc(100% / 3 - #{vw(136)} * 2 / 3);
      border-bottom: vw(1) solid #ffffff;
      font-weight: 300;
      &,
      &::placeholder {
        font-size: vw(30);
        line-height: vw(30);
        @extend %white;
      }
    }
  }
  &-button {
    margin-top: vw(40);
    padding-left: vw(1290);
    position: relative;
    z-index: 2;
  }
}
